
<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">作业</p>
            <p class="navBtn f24 cfff pl-30" @click="send">发布</p>
        </div>
        <div class="posiFixs">
            <div class="contentBox mt-84">
                <div class="down f30 c000 subject" @click="subjectChange">{{nowSbject}}<img class="tranrotate" :class="{'tranrotateTo0': !modelShow,'tranrotateTo180':modelShow}" src="../../../assets/index/down.png" alt=""></div>
                <div class="down f30 c000 subject" @click="levelChange">{{nowClass}}<img class="tranrotate"  :class="{'tranrotateTo0': !modelShow2,'tranrotateTo180':modelShow2}" src="../../../assets/index/down.png" alt=""></div>
            </div>
            <van-popup class="piker-com" v-model="modelShow" position="top" :overlay="true">
                <van-picker
                title=""
                :columns="columns"
                @cancel="onCancel"
                @confirm="onConfirm"
                @change="onChange"
                show-toolbar
                ref="pikers"
                />
            </van-popup>
            <van-popup class="piker-com" v-model="modelShow2" position="top" :overlay="true">
                <van-picker
                title=""
                :columns="columnClass"
                @cancel="onCancel1"
                @confirm="onConfirm1"
                show-toolbar
                
                />
            </van-popup>
            <!-- <div class="grounpBtn">11</div> -->
        </div>
        <div class="item-for">
            <router-link class="item-list" v-for="(item,index) in list" :key="index" :to="{path:'/homeworkdetail',query:{id:item.id}}">
                <div class="f30 home-lin1">
                    <p class="item-title c000">{{item.title}}</p>
                    <!-- <img class="item-img" src="../../../assets/index/noRead.png" alt=""> -->
                </div>
                <div class="f24 cc3 home-lin2">
                    <p class="item-con">{{item.work_desc}}</p>
                    <span class="item-time">{{item.create_time | fileTime(item.create_time )}}</span>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            columns: [],
            columnsData:[],
            columnClass: [],
            columnClassData: [],
            show:true,
            subject:true,
            level:true,
            modelShow:false,//科目下拉
            modelShow2:false,//班级下拉
            nowSbject:'',//默认科目
            nowSbjectIndex:0,//默认科目下标
            nowSbjectId:0,//默认科目id
            nowClass:'',//默认班级
            nowClassId:0,//默认班级id
            nowClassIndex:0,//默认班级下标
            list:[],//列表
            timer: null 
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num);
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'})
        },
        onConfirm(value, index) {
            this.modelShow = !this.modelShow;
            this.nowSbject = value;
            this.nowSbjectId = this.columnsData[index].courses_id;
            this.getHomework();//选择后调用列表
        },
        onCancel(value, index) {
            
            this.modelShow = !this.modelShow;
            this.$refs.pikers.setColumnValue(this.nowSbjectIndex,this.nowSbject);
        },
        onConfirm1(value, index) {
            this.modelShow2 = !this.modelShow2;
            this.nowClass = value;
            this.nowClassId = this.columnClassData[index].classes_id;
            this.getHomework();//选择后调用列表
        },
        onCancel1(picker, value, index) {    
            this.modelShow2 = !this.modelShow2;
        },
        onChange(picker, value, index) {
            console.log(picker, value, index)
        },
        send(){
            this.$router.push({path:'/sethomework'})
        },
        subjectChange(e){
            if(this.modelShow2){
                this.modelShow2 = !this.modelShow2
            }
            this.modelShow = !this.modelShow;
        },
        levelChange(e){
            if(this.modelShow){
                this.modelShow = !this.modelShow;
            }
            this.modelShow2 = !this.modelShow2
        },
        getSubject(){//获取所有科目
            var nowSbject = this.nowSbject;
            this.$http({url:this.$api.homework.getSubject},{teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.columnsData = data.data;
                    var columns = [];
                    for(var i in data.data){
                        columns.push(data.data[i].courses_name)
                    }
                    this. columns = columns;
                    if(this.nowSbjectId == 0){
                        this.nowSbject = columns[0];
                        this.nowSbjectId = data.data[0].courses_id;
                    }
                }
                
            }) 
        },
        getClass(){//获取所有的班级
            this.$http({url:this.$api.homework.getClass},{teacherId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.columnClassData = data.data;
                    var columnClass = [];
                    for(var i in data.data){
                        columnClass.push(data.data[i].coursesName)
                    }
                    this.columnClass = columnClass;
                    if(this.nowClassId == 0){
                        this.nowClass = columnClass[0];
                        this.nowClassId = data.data[0].classes_id;
                    }
                }
                
            }) 
        },
        getHomework(){
            this.$http({url:this.$api.homework.getHomework},{classesId:this.nowClassId,coursesId:this.nowSbjectId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }else{
                    this.$toast(data.msg);
                }
            })
        },
        timerGetHomework(){
            var that = this;
            var timer = setInterval(() =>{
                if(that.nowClassId == -1 || that.nowSbjectId == -1){//防止这个老师没有可选班级或者没有可选科目
                    clearInterval(timer);
                    return
                }
                var columns = that.columns;
                var columnClass = that.columnClass;
                if(columns.length != 0 && columnClass.length != 0){
                    clearInterval(timer);
                    that.getHomework();
                }
            },100)
        }
    },
    created() {
        this.getSubject();//获取所有科目
        this.getClass();//获取所有班级
        this.timerGetHomework();
    },
}
</script>

<style scoped>
    .posiFixs{position: relative;width: 100%;overflow: hidden;}
    .contentBox{height: 1rem;border-bottom: 1px solid #f2f2f2;position: fixed;width: 100%;z-index: 9999;background: #fff;}
    /* 修改组件样式 */
    .piker-com{top: 1.83rem;position: fixed;width: 101%;z-index: 9999999;}
    .van-picker-column{height: 100px !important;z-index: 99999;}
    .van-picker-column{top: -0.8rem;}
    .van-hairline--top-bottom{top: 220px !important;}
    .van-picker{padding-bottom: 1rem;}
    
    /* 修改组件样式结束 */
    .down{width: 50%;height: 0.7rem;line-height: 0.7rem;float: left;text-align: center;margin-top: 0.15rem;box-sizing: border-box;}
    .subject{border-right:1px solid #f3f3f5; }
    .tranrotate{transition:transform 0.2s;}
    .tranrotateTo0{transform:rotate(0deg)}
    .tranrotateTo180{transform:rotate(-180deg)}
    .item-for{width: 100%;overflow: hidden;padding-bottom: 1rem;margin-top: 1.84rem;}
    .item-list{height: 1.6rem;margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;box-sizing: border-box;padding: 0.35rem 0.3rem 0.3rem 0;overflow: hidden;}
    .item-title{float: left;}
    .item-img{float: right;margin-top: 0.12rem;width: 0.14rem;height: 0.14rem;display: block;}
    .home-lin1{clear: both;overflow: hidden;}
    .home-lin2{clear: both;margin-top: 0.2rem;overflow: hidden;}
    .item-con{float: left;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;width: 2rem;}
    .item-time{float: right;}
    /* .grounpBtn{background: red;height: 1rem;width: 100%;position:absolute;bottom: 0;z-index:99999;} */
</style>





